import * as React from 'react';
import type { SinglePilet } from 'piral-base';

const SecondPiletDiv = ({ authToken, children, piral }) => {
	
    return <div className='second-pilet-div' style={{background:"yellow"}}>
		<p>SecondPiletDiv</p>
		<p>AuthToken is {authToken}. This is passed from props</p>
		<p>This is fetched from piral.getdata --- foo: {piral.getData("foo")}</p>
        {children}
    </div>
}

// allow-unused-export -- pulled in via require()
export const secondPilet: SinglePilet = {
	name: 'secondpilet',
	version: '0.0.1',
	spec: 'v2',
	dependencies: {},
	config: {},
	basePath: '',
	link: 'fakelink',
	setup(api) {
		
		api.registerExtension('dashboard-second-pilet', ({piral}) => {
			return <SecondPiletDiv authToken="token12345" piral={piral} >This came through a pilet extension slot</SecondPiletDiv>
		});
	},
};